/*
    ===============================
            Portfolio Styles
    ===============================
*/

.portfolio {
  background: #f8f8f8;
  padding-top: 20px;
  overflow: hidden;
}

.portfolio .portfolio-title {
  text-align: center;
  font-size: 35px;
}

.portfolio .portfolio-title span {
  font-weight: normal;
}

.portfolio .portfolio-list {
  list-style: none;
  text-align: center;
  margin: 20px 0;
}

.portfolio .portfolio-list .portfolio-item {
  display: inline-block;
  width: 100px;
  padding: 10px;
}

.portfolio .portfolio-list .portfolio-item.active {
  background: #eb5424;
  color: #fff;
}

.box div {
  width: 25%;
  float: left;
  font-size: 0;
  position: relative;
}

.box div img {
  width: 100%;
}

.box div .overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(235, 84, 36, 0.5);
  font-size: 15px;
  opacity: 0;
}

.box div .overlay span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  display: block;
  padding: 10px 20px;
  cursor: pointer;
  border: 2px solid #eb5424;
}

.box div:hover .overlay {
  opacity: 1;
}

@media (max-width: 575px) {
  .portfolio .portfolio-list .portfolio-item {
    display: block;
    margin: auto;
  }

  .box div {
    width: 100%;
  }
}

@media (min-width: 576px) and (max-width: 768px) {
  .box div {
    width: 50%;
  }
}

/*
  ===============================
     Profile + Skills Styles
  ===============================
*/

.profile_skills {
  padding: 50px 0;
  overflow: hidden;
}

.profile_skills .profile {
  width: 50%;
  float: left;
}

.profile_skills .profile .profile-list {
  list-style: none;
}

.profile_skills .profile .profile-list .profile-item {
  margin-bottom: 8px;
}

.profile_skills .profile .profile-list .profile-item span {
  display: inline-block;
  width: 100px;
  font-weight: bold;
}

.profile_skills .profile .profile-list .profile-item span.web {
  font-weight: normal;
  color: #eb5424;
}

.profile_skills .skills {
  width: 50%;
  float: left;
}

.profile_skills .skills .skills-desc {
  font-size: 15px;
  color: #888;
  line-height: 1.5;
  margin-bottom: 20px;
}

.profile_skills .skills .bar {
  overflow: hidden;
  padding: 10px 0;
  margin-bottom: 10px;
}

.profile_skills .skills .bar .title {
  float: left;
}

.profile_skills .skills .bar .perc {
  float: right;
  margin-right: 100px;
}

.profile_skills .skills .bar .parent {
  height: 2px;
  clear: both;
  background: #f8f8f8;
  position: relative;
  top: 5px;
}

.profile_skills .skills .bar .parent span {
  background: #eb5424;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
}

.profile_skills .skills .bar .parent span.sp1 {
  width: 100%;
}

.profile_skills .skills .bar .parent span.sp2 {
  width: 90%;
}

.profile_skills .skills .bar .parent span.sp3 {
  width: 80%;
}

.profile_skills .profile .profile-title,
.profile_skills .skills .skills-title {
  font-size: 40px;
  margin-bottom: 20px;
}

.profile_skills .profile .profile-title span,
.profile_skills .skills .skills-title span {
  font-weight: normal;
}

@media (max-width: 768px) {
  .profile_skills .profile,
  .profile_skills .skills {
    width: 100%;
    float: none;
  }
  .profile_skills .profile {
    margin-bottom: 20px;
  }
}

/*
  ===============================
          Creative Styles
  ===============================
*/

.creative {
  height: 500px;
  background: url("../creative//about-bg.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.creative .creative-info {
  padding-top: 100px;
  width: 50%;
  float: right;
}

.creative .creative-info .info-title {
  font-weight: bold;
  font-size: 50px;
}

.creative .creative-info .info-title span {
  font-weight: normal;
}

.creative .creative-info .info-dir {
  font-size: 40px;
  color: #eb5424;
  margin-bottom: 20px;
}

.creative .creative-info .info-desc {
  color: #000;
  margin-bottom: 15px;
  line-height: 1.8;
}

.creative .creative-info .info-desc a {
  text-decoration: none;
}

@media (max-width: 991px) {
  .creative {
    height: auto;
  }
  .creative .creative-info {
    width: 100%;
    padding-top: 10px;
    float: none;
  }

  .creative .creative-info .info-dir {
    font-size: 30px;
  }
}
